$vertical-padding: 30px;
$headers-els-pseudos: 20px;

.header {
  position: relative;
  z-index: 2;

  padding: $vertical-padding 0 $vertical-padding;

  color: $color-default-white;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: 0;

    width: calc(736/1440*100%);
    height: 354px;
    overflow: hidden;

    background: url("../img/svg/wave.svg") no-repeat top right;
    background-size: 100% 100%;
    transform: translateZ(0);

    transition:
      height $trans-default,
      width $trans-default,
      background-size $trans-default;

    pointer-events: none;

    @include vp-1023 {
      width: 75%;
    }

    @include vp-767 {
      width: 99%;
      height: 206px;
    }
  }

  &--sticky {
    position: sticky;
    top: 0;
    left: 0;

    width: 100%;
  }

  &--opaque {
    background-color: $color-default-black;

    &.header::after {
      height: 100%;

      background-size: 100% 100%;
    }
  }

  &__grid {
    display: flex;
    gap: 20px;

    @include vp-1023 {
      flex-direction: column;
      gap: 0;
    }
  }

  .logo {
    flex-shrink: 0;
    align-self: start;
  }

  &__nav {
    display: flex;
    flex-grow: 1;
    align-items: start;
    gap: 20px;

    //                                        contact us
    & > .header__item {
      position: relative;

      flex-shrink: 0;

      @include vp-1023 {
        position: fixed;
        bottom: $headers-els-pseudos;

        align-self: end;

        &::after {
          content: "";
          position: absolute;
          top: -$headers-els-pseudos;
          right: -$padding-container-1023;
          bottom: -$headers-els-pseudos;
          left: -$padding-container-1023;
          z-index: -1;

          background-color: $color-default-black;
        }
      }

      @include vp-767 {
        right: $padding-container-767;
        left: $padding-container-767;
      }
    }

    @include vp-1023 {
      flex-direction: column;
      align-items: stretch;
      height: 0;

      transform: translate(0, calc(-100%));
      visibility: hidden;
      opacity: 0;

      transition:
        transform $trans-default,
        opacity $trans-default,
        visibility $trans-default;
    }
  }

  &__list {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;

    list-style: none;
    gap: 4px 18px;

    @include vp-1023 {
      display: grid;
      flex-grow: unset;
      grid-template-columns: 1fr 1fr;
    }

    @include vp-767 {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      justify-content: start;
    }
  }

  &__link {
    display: block;
    padding: 10px 22px;

    transition: opacity $trans-default;

    user-select: none;

    &--gradient-border {
      border-width: 1px;
      border-style: solid;
      border-image: $gradient;
      border-image-slice: 1;
    }

    @include hover-focus {
      opacity: 0.8;
    }

    &.is-active {
      font-weight: 500;

      @include hover-focus {
        cursor: default;
        opacity: 1;
      }
    }
  }

  &__toggle {
    display: none;

    background: url("../img/svg/menu-toggle-closed.svg") no-repeat center;
    background-color: transparent;
    background-size: 16px 16px;

    @include vp-1023 {
      position: fixed;
      top: 40px;
      right: 24px;
      z-index: 1;//

      display: block;
      width: 32px;
      height: 32px;

      border-width: 1px;
      border-style: solid;
      border-image: $gradient;
      border-image-slice: 1;
    }

    @include vp-767 {
      right: 16px;
    }
  }


  /////////// / / /               IS OPEN
  &.is-open {
    height: 100vh;
    /* stylelint-disable-next-line */
    height: 100dvh;
    padding: $vertical-padding 0 $vertical-padding;
    overflow-y: auto;

    background-color: $color-default-black;

    .logo {
      position: fixed;

      &::after {
        content: "";
        position: absolute;
        top: -$vertical-padding;
        left: 0;
        z-index: -1;

        width: 100vw;
        height: calc(100% + $vertical-padding * 2);

        background-color: $color-default-black;
      }
    }

    .container,
    .header__grid {
      height: 100%;
    }

    .header__nav {
      flex-grow: 1;
      justify-content: space-between;
      margin-top: 80px;

      transform: unset;
      visibility: unset;
      opacity: unset;
    }

    .header__toggle {
      top: 40px;

      background-image: none;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: 18px;
        height: 2px;

        background-color: $color-default-white;
        transform: translate(-50%, -50%) rotate(45deg);
      }

      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: 18px;
        height: 2px;

        background-color: $color-default-white;
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }

    &::after {
      height: 0;
    }
  }
}
